.recently-played {

    table  {
        border: none;
        border-collapse: collapse;
        width: 50%;
        img {
            height: 50px;
            width: auto;
        }
    }
    td {
        border: none; /* This sets the border for all sides */
        padding: 8px;
        text-align: left;
    }
    tr {
        border-bottom: 1.5px solid #dddddd; /* This sets the bottom border for each row */
    }
    tr:last-child {
        border-bottom: none; /* Remove the bottom border for the last row to avoid double lines */
    }
}

.token {
    overflow: auto;
}